import React, { useState } from "react";

export default function App() {
  const questions = [
    {
      questionText: "How many states made up the United States before Alaska and Hawaii joined?",
      answerOptions: [
        { answerText: "50", isCorrect: false },
        { answerText: "49", isCorrect: false },
        { answerText: "52", isCorrect: false },
        { answerText: "48", isCorrect: true }
      ]
    },
    {
      questionText: "How many stars are there on the American flag?",
      answerOptions: [
        { answerText: "48", isCorrect: false },
        { answerText: "50", isCorrect: true },
        { answerText: "49", isCorrect: false },
        { answerText: "52", isCorrect: false }
      ]
    },
    {
      questionText: "If I have five apples and you have four apples, how many more apples do I have?",
      answerOptions: [
        { answerText: "4", isCorrect: false },
        { answerText: "9", isCorrect: false },
        { answerText: "5", isCorrect: false },
        { answerText: "1", isCorrect: true }
      ]
    },
    {
      questionText: "A hexagon has how many sides?",
      answerOptions: [
        { answerText: "4", isCorrect: false },
        { answerText: "6", isCorrect: true },
        { answerText: "5", isCorrect: false },
        { answerText: "7", isCorrect: false }
      ]
    }
  ];

  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [showScore, setShowScore] = useState(false);
  const [score, setScore] = useState(0);

  const handleAnswerOptionClick = (isCorrect) => {
    if (isCorrect) {
      setScore(score + 1);
    }

    const nextQuestion = currentQuestion + 1;
    if (nextQuestion < questions.length) {
      setCurrentQuestion(nextQuestion);
    } else {
      setShowScore(true);
    }
  };
  return (
    <div>
      <div></div>
      <div className="app">
        {showScore ? (
          <div className="score-section">
            You scored {score} out of {questions.length}
          </div>
        ) : (
          <>
            <div className="question-section">
              <div className="question-count">
                <span>Question {currentQuestion + 1}</span>/{questions.length}
              </div>
              <div className="question-text">
                {questions[currentQuestion].questionText}
              </div>
            </div>
            <div className="answer-section">
              {questions[currentQuestion].answerOptions.map((answerOption) => (
                <button
                  onClick={() =>
                    handleAnswerOptionClick(answerOption.isCorrect)
                  }
                >
                  {answerOption.answerText}
                </button>
              ))}
            </div>
          </>
        )}
      </div>
    </div>
  );
}
